const connection = require('./config/database');

const debugQuery = `
SELECT r.id_reservasi, r.status, p.metode_pembayaran, p.jumlah, p.status_pembayaran
FROM reservasi r
LEFT JOIN pembayaran p ON p.id_reservasi = r.id_reservasi
WHERE r.status = 'Selesai'
ORDER BY r.id_reservasi DESC;
`;

connection.query(debugQuery, (err, results) => {
  if (err) {
    console.error('Error:', err);
    return;
  }

  console.log('Results for Selesai reservations:');
  console.table(results);

  // Check what getAll returns for these records
  const Model_Reservasi = require('./models/Model_Reservasi');

  Model_Reservasi.getAll().then(allData => {
    console.log('\nWhat getAll() returns for Selesai records:');
    const selesaiRecords = allData.filter(r => r.status === 'selesai');
    console.table(selesaiRecords.map(r => ({
      id_reservasi: r.id_reservasi,
      status: r.status,
      metode_pembayaran: r.metode_pembayaran,
      jumlah: r.jumlah,
      status_pembayaran: r.status_pembayaran,
      bukti_pembayaran: r.bukti_pembayaran
    })));

    connection.end();
  }).catch(err => {
    console.error('Error getting all data:', err);
    connection.end();
  });
});
